* {
  font-family: Arial, Helvetica, sans-serif;
}

.App {
  height: 140vh;
  background-color: #002043;
}

.main-page-cont {
  width: 99.9%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  background: linear-gradient(55deg, #002043 10%, #203c5b 20%, #002043 35%, #002043 40%, #203c5b 55%, #002043 75%, #203c5b 95%)
}

.search-input {
  width: 100%;
  height: 10%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.input-search {
  border: 0;
  border-radius: 30px 30px 30px 30px;
  padding: 0 10px 0 10px;
  text-align: center;
  color: black;
  height: 25px;
  width: 500px;
  font-size: 25px;
  font-weight: 900;
}

.movie-list {
  display: grid;
  align-items: center;
  width: 95%;
  height: 35%;
  padding: 1.5% 1.5%;
  border: solid #bf5709 5px;
  background: linear-gradient(185deg, #112020 25%, #2d3838 50%, #112020 75%);
  border-radius: 5px;
  grid-auto-flow: column;
  grid-auto-columns: 15%;
  overflow-x: auto;
  overscroll-behavior-inline: contain;
  p {
    text-decoration: none;
  }
}

.movie {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 70%;
  height: 80%;
  img {
    border-radius: 5px;
    width: 100%;
    height: auto;
    object-fit: contain;
    cursor: pointer;
  }
  p {
    font-size: 15px;
    text-align: center;
    text-wrap: pretty;
    color: white;
    margin: 10px 0 0;
  }
}

.movie img:hover {
  -webkit-transform: scale(1.1);
  -ms-transform: scale(1.1);
  transform: scale(1.1);
}

::-webkit-scrollbar {
  width: 12px;
  height: 12px;
}

::-webkit-scrollbar-thumb {
  background-color: #0163cb;
  border-radius: 5px;
}

::-webkit-scrollbar-track {
  background-color: #01143a;
}

@media screen and (max-width: 576px) {
  .main-page-cont {
    background-color: #0163cb;
  }

  .movie {
    img {
      height: 100px;
      width: 100px;
    }
    p {
      font-size: 12px;
    }
  }

  .search-input {
    width: 75%;
  }

  .input-search {
    font-size: 15px;
  }

  .movie-list {
    padding: 1% 1%;
  }
}